.tu-checkbox {
  display: inline-flex;
  align-items: center;
  color: #9baacf;
  font-weight: 500;
  font-size: 14px;
  white-space: nowrap;
  outline: none;
  cursor: pointer;
  margin-right: 30px; }
  .tu-checkbox:hover .tu-checkbox__inner::after {
    border-color: #6d5dfc; }
  .tu-checkbox.is-bordered {
    padding: 6px calc(8px + 6px) 6px 8px;
    color: #9baacf;
    background: transparent;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    box-sizing: border-box;
    border-radius: 4px; }
    .tu-checkbox.is-bordered .tu-checkbox__inner {
      box-shadow: none; }
    .tu-checkbox.is-bordered .tu-checkbox__label {
      padding-left: 0; }
    .tu-checkbox.is-bordered.is-checked {
      box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
      .tu-checkbox.is-bordered.is-checked .tu-checkbox__inner {
        box-shadow: none; }
  .tu-checkbox.is-disabled {
    cursor: not-allowed;
    position: relative; }
    .tu-checkbox.is-disabled .tu-checkbox__inner::after, .tu-checkbox.is-disabled .tu-checkbox__inner:hover::after, .tu-checkbox.is-disabled .tu-checkbox__inner:focus::after, .tu-checkbox.is-disabled .tu-checkbox__inner:active::after {
      border-color: #bec8e4; }
    .tu-checkbox.is-disabled .tu-checkbox__label {
      color: #bec8e4; }
    .tu-checkbox.is-disabled.is-checked .tu-checkbox__inner::after {
      border-color: #a79efd; }
  .tu-checkbox.is-checked .tu-checkbox__inner {
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
    .tu-checkbox.is-checked .tu-checkbox__inner::after {
      border-color: #6d5dfc; }
  .tu-checkbox.is-focus .tu-checkbox__inner::after {
    box-shadow: 0 0 3px #8abdff; }
  .tu-checkbox--mini.is-bordered {
    padding: 3px calc(4px + 6px) 3px 4px; }
    .tu-checkbox--mini.is-bordered .tu-checkbox__inner {
      width: 14px;
      height: 14px; }
    .tu-checkbox--mini.is-bordered .tu-checkbox__label {
      font-size: 10px; }
  .tu-checkbox--small.is-bordered {
    padding: 6px calc(8px + 6px) 6px 8px; }
    .tu-checkbox--small.is-bordered .tu-checkbox__inner {
      width: 16px;
      height: 16px; }
    .tu-checkbox--small.is-bordered .tu-checkbox__label {
      font-size: 12px; }
  .tu-checkbox--large.is-bordered {
    padding: 9px calc(13px + 6px) 9px 13px; }
    .tu-checkbox--large.is-bordered .tu-checkbox__inner {
      width: 22px;
      height: 22px; }
    .tu-checkbox--large.is-bordered .tu-checkbox__label {
      font-size: 14px; }
  .tu-checkbox__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-flex;
    line-height: 1;
    position: relative;
    vertical-align: middle; }
    .tu-checkbox__inner {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      position: relative;
      box-sizing: border-box;
      width: 20px;
      height: 20px;
      border-radius: 2px;
      box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
      .tu-checkbox__inner::after {
        box-sizing: content-box;
        content: "";
        border: 2px solid #9baacf;
        border-left: 0;
        border-top: 0;
        height: 50%;
        transform: translate(0, -10%) rotate(45deg) scaleY(1);
        width: 25%;
        transition: 0.3s ease;
        transform-origin: center; }
    .tu-checkbox__original {
      opacity: 0;
      outline: none;
      position: absolute;
      margin: 0;
      width: 0;
      height: 0;
      z-index: -1; }
  .tu-checkbox__label {
